，每100克鱼肉中含水分64.1克、蛋白质14.4克，并含有多种矿物质和微量元素，特别适合体弱虚损、营养不良之人食用。\n\n       除鲶鱼的鱼子有杂味不宜食用以外，全身是宝，鲶鱼是名贵的营养佳品，早在史书中就有记载，可以和鱼翅、野生甲鱼相媲美，它的食疗作用和药用价值是其他鱼类所不具备的，独特的强精壮骨和益寿作用是它独具的亮点。\n\n      胡子鲶刺少肉多，肉质细嫩，营养价值较高，具有滋补，敛肌活血，治疗小儿疳积、妇女倒经及哮喘等作用。\n\n     塘角鱼肉质细嫩，味道鲜美，肌肉刺少，营养丰富，很受人们喜爱。同时，它还具有收敛肌肉、愈合伤口的功效，可作为外科手术后病人的滋补佳品。"),_c('br'),_vm._v("\n     【营养成分】每100g鲶鱼肉含蛋白质14.4g,脂肪20.6g,热量1015.7kJ。"),_c('br'),_vm._v("\n     性味】甘、平、无毒。人脾、肾。"),_c('br'),_vm._v("\n\n   【功效】补血、滋肾,调中,兴阴。"),_c('br'),_vm._v("\n\n   【功能】滋阴养胃,催乳,利尿。主要用于脾胃不和,产后缺乳,浮肿,腰膝酸痛,鼻衄不止。"),_c('br'),_vm._v("\n\n  【验方】1.产后缺乳:用鲶鱼1条,去肠杂,熬汤,打荷包蛋,连续服用。2.治肾虚腰疼或耳聋:胡子鲶1条,黑豆100g,煮熟之。3.身体虚弱,经常鼻衄者:用胡子鲶l条,加糯米、油、盐熟食之,可断根。\n   ")])])])},function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',{staticClass:"panel panel-default"},[_c('div',{staticClass:"panel-heading"},[_c('div',{attrs:{"id":"ftl-11"}},[_c('div',{attrs:{"id":"qusta"}},[_vm._v("当前食品安全条件下吃鱼知识问答")])])]),_vm._v(" "),_c('div',{staticClass:"panel-body"},[_c('h2',[_vm._v("\n      问题：鱼类污染主要有哪些?"),_c('br')]),_vm._v(" "),_c('h3',[_vm._v(" 鱼体内的污\n       染物主要来源于地理环境和人类活动造成的环境污染。生活在污水中的鱼，会富集水中的有毒金属和其他有害物质。在鱼类的各种污染物中，危害最大的是汞，它会侵犯人体中枢神经系统，造成语言和记忆障碍等，对胎儿和儿童尤为\n        明显，可能会影响智力。\n    ")]),_vm._v(" "),_c('div',{staticClass:"signal"},[_vm._v(" ")]),_vm._v(" "),_c('h2',[_vm._v("\n       问题：鱼类遭污染，我们还能吃吗?"),_c('br')]),_vm._v(" "),_c('h3',[_vm._v("\n　      即使鱼类有遭受污染的可能，吃鱼仍利大于弊。研究发现，吃鱼有助于保护视力、降低心脏病风险、促进发育、保护皮肤、提高脑力和精子数量、预防哮喘和关节疾病等。美国食品和药品管理局于今年初发   \n       布的“鱼类消费建议”指出，成年人每周应食用2～3份低汞鱼或贝类，约227～340克(烹饪前的可食部重量)。常见低汞鱼包括鳕鱼、鲑鱼、胡子鲶等;\n    ")]),_vm._v(" "),_c('div',{staticClass:"signal"},[_vm._v(" ")]),_vm._v(" "),_c('h2',[_vm._v("\n     问题：有的鱼尝起来有煤油味，网上披露说是为了在运输过程中保持鱼的存活率，这是真的吗?"),_c('br')]),_vm._v(" "),_c('h3',[_vm._v("\n　　  \n      运输过程中为了保持鱼鲜活，有的商贩会加入孔雀石绿，不过这是非法的，也不会产生煤油味。煤油味或其他异味，可能是鱼本身来自被污染水体，或运输过程中受到污染，也可能是商贩加入汽油等化学物质刺激鱼活动造成的。具体原因很难推测，但只要鱼出现异味，就一定存在问题，消费者不应购买、食用。\n    ")]),_vm._v(" "),_c('div',{staticClass:"signal"},[_vm._v(" ")]),_vm._v(" "),_c('h2',[_vm._v("\n    　问题：怎么吃鱼可以将风险降到最低?"),_c('br')]),_vm._v(" "),_c('h3',[_vm._v("\n　　除了鱼的种类，鱼龄和体型越大，体内积聚的有害物质就可能越多。建议买鱼选个头“八分大”左右的，如鲤鱼和武昌鱼以1斤半、鲫鱼0.5～1斤、塘角鱼0.2～0.4斤，草鱼4～5斤的为宜。吃鱼种类应尽量丰富些，有条件的话还应多吃些虾、蟹、蚝等贝类。可以通过望、闻、按来挑选鱼：好的鱼应该是鱼眼饱满凸出，角膜透明清亮、富有弹性，鱼身有透明的黏液，鳞片有光泽，且与鱼体贴附紧密，未有脱落;闻一闻鱼鳃，海水鱼应该具有咸腥味，淡水鱼则是土腥味，如果有酸味或煤油味等异味，就不要购买了;最后，用手指按压一下鱼身，好的鱼肉质结实有弹性，指压后凹陷立即消失。\n\n　　烹调方法方面，蒸最为健康;煮或炖位居其次，部分营养可能会流失于汤水中;如果将鱼油炸，损失可就大了，不仅破坏DHA这类不饱和脂肪酸，使鱼失去低脂优势，还容易产生有害物质。需要提醒的是，鱼籽由于胆固醇较高，“三高”人群最好少吃或将其剔除,对于健康人群，鱼子是鱼热量最高的可食用部分。\n    ")])])])},function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',{attrs:{"id":"pic"}},[_c('div',{staticClass:"carousel slide",attrs:{"id":"myCarousel"}},[_c('ol',{staticClass:"carousel-indicators"},[_c('li',{staticClass:"active",attrs:{"data-target":"#myCarousel","data-slide-to":"0"}}),_vm._v(" "),_c('li',{attrs:{"data-target":"#myCarousel","data-slide-to":"1"}}),_vm._v(" "),_c('li',{attrs:{"data-target":"#myCarousel","data-slide-to":"2"}})]),_vm._v(" "),_c('div',{staticClass:"carousel-inner"},[_c('div',{staticClass:"item active"},[_c('img',{attrs:{"src":require("../../../static/images/gc1.jpg"),"alt":"First slide"}})]),_vm._v(" "),_c('div',{staticClass:"item"},[_c('img',{attrs:{"src":require("../../../static/images/gc2.jpg"),"alt":"Second slide"}})]),_vm._v(" "),_c('div',{staticClass:"item"},[_c('img',{attrs:{"src":require("../../../static/images/gc3.jpg"),"alt":"Third slide"}})])]),_vm._v(" "),_c('a',{staticClass:"left carousel-control",attrs:{"href":"#myCarousel","role":"button","data-slide":"prev"}},[_c('span',{staticClass:"glyphicon glyphicon-chevron-left",attrs:{"aria-hidden":"true"}}),_vm._v(" "),_c('span',{staticClass:"sr-only"},[_vm._v("Previous")])]),_vm._v(" "),_c('a',{staticClass:"right carousel-control",attrs:{"href":"#myCarousel","role":"button","data-slide":"next"}},[_c('span',{staticClass:"glyphicon glyphicon-chevron-right",attrs:{"aria-hidden":"true"}}),_vm._v(" "),_c('span',{staticClass:"sr-only"},[_vm._v("Next")])])])])},function () {var _vm=